



















































import Vue from "vue";

class RequestForm {
  title: string = "";
  type: string = "";
  isProduction: boolean = false;
  desc: string = "";
}

export default Vue.extend({
  data() {
    return {
      request: new RequestForm()
    };
  },
  methods: {
    onSubmit(): void {
      alert("登録しました");
      this.request = new RequestForm();
    }
  }
});
