








import Vue from "vue";

export default Vue.extend({
  data() {
    return {
      msg: "Hello Vue.js!",
      num1: 1,
      num2: 2
    };
  },
  computed: {
  computedMsg(): string {
    return this.msg.replace(/!/g, "") + " + TypeScript!";
  },
  calcMsg(): number {
    return this.num1 + this.num2
  }
}
});
